import { useState, useEffect } from "react";




const Index = () => {
    const [ background, setBackground ] = useState({ loaded: true, timer: false });
    const scrollerClassNames = [ "scroller-top", "scroller-middle", "scroller-bottom", ];

    // Fog delay and transition styling
    const fogFadeIn = 5555;
    const fogDelay = 1.5;
    const foggyLettersClear = {
        opacity: 0,
        transition: `${fogFadeIn}ms ease opacity ${fogDelay}s`,
    };
    const foggyLettersOpaque = {
        opacity: 1,
        color: red,
        transition: `${fogFadeIn}ms ease opacity ${fogDelay}s`,
    };
    const [ fogScroll, setFogScroll ] = useState({ ...foggyLettersClear });

    // loading trigger to turn fog on
    useEffect(() => {
        if (fogScroll === null) setFogScroll({ ...foggyLettersClear });
        const timer = setTimeout(() => {
            setFogScroll({ ...foggyLettersOpaque })
        }, 2500);
        return () => clearTimeout(timer);
    }, []);

    return (
        <>
            <section className="section__index" style={{ backgroundImage: `url(/static/matthew-ronder-seid-xYd99V3S5aI-unsplash-medium.jpg)` }} >
                <div className="block__greeting"  >
                    <strong>
                        <span>le&nbsp;</span>tanque
                    </strong>
                </div>
            </section>

            {/* <ProgressiveImage
                preview="/static/matthew-ronder-seid-xYd99V3S5aI-unsplash-small.jpg"
                src="/static/matthew-ronder-seid-xYd99V3S5aI-unsplash-medium.jpg"
                transitionTime={500}
                transitionFunction="ease"
                render={(src, style) => (
                    <section className="section__index" style={Object.assign(style, { backgroundImage: `url(${src})` })} >

                        {style.filter === "blur(0px)" && !background.loaded && setBackground({ ...background, loaded: true })}

                        {background.loaded && (
                            <div className="block__greeting"  >
                                <strong>
                                    <span>le&nbsp;</span>tanque
                                </strong>
                            </div>
                        )}
                    </section>
                )}
            /> */}

            {scrollerClassNames.map((position, index) => (
                <section className={`section__scroller-container ${position}`} key={index}  style={fogScroll}  >
                    <div className="block__scroller">
                        <strong>frank le tanque portfolio</strong>
                    </div>
                </section>
            ))}

        </>
    )
};

Index.getInitialProps = async function () {
    return { 
        transitionType: 'none',
        timeout: 0
    }
}

export default Index;

